import { Component, OnInit } from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import { AuthenticationService } from '../../../../shared/data/authentication.service';
@Component({
  selector: 'app-payment-failed',
  templateUrl: './payment-failed.component.html',
  styleUrls: ['./payment-failed.component.scss']
})
export class PaymentFailedComponent implements OnInit {
//a;
constructor(public router: Router, private route: ActivatedRoute, public authenticationService: AuthenticationService) {
  //localStorage.removeItem('tdetails');
  debugger;
  if(localStorage.getItem('tdetails')){
    let userData = JSON.parse(localStorage.getItem('tdetails'));
    userData.status = 'failed';
    this.authenticationService.addtransaction(userData).then(data => {
      localStorage.removeItem('tdetails');
    });
  }
}

  ngOnInit() {
  }

  onLogin() {
      this.router.navigate(['login'], { relativeTo: this.route.parent });
  }

}
