import { Component, OnInit } from '@angular/core';
import { AuthenticationService } from '../../../../shared/data/authentication.service';
import { Router, ActivatedRoute } from "@angular/router";

@Component({
  selector: 'app-success-payment',
  templateUrl: './success-payment.component.html',
  styleUrls: ['./success-payment.component.scss']
})
export class SuccessPaymentComponent implements OnInit {

  constructor(public authenticationService: AuthenticationService, public router: Router, private route: ActivatedRoute) {
    debugger;
    if(localStorage.getItem('tdetails')){
      let userData = JSON.parse(localStorage.getItem('tdetails'));
      userData.status = 'success';
      this.authenticationService.addtransaction(userData).then(data => {
        localStorage.removeItem('tdetails');
      });
    }
  }

  ngOnInit() {
  }

  onLogin() {
      this.router.navigate(['login'], { relativeTo: this.route.parent });
  }

}
