import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ComingSoonPageComponent } from "./coming-soon/coming-soon-page.component";
import { ErrorPageComponent } from "./error/error-page.component";
import { ForgotPasswordPageComponent } from "./forgot-password/forgot-password-page.component";
import { LockScreenPageComponent } from "./lock-screen/lock-screen-page.component";
import { LoginPageComponent } from "./login/login-page.component";
import { MaintenancePageComponent } from "./maintenance/maintenance-page.component";
import { OrgRegisterComponent } from "./org-register/org-register.component";
import { RegisterPageComponent } from "./register/register-page.component";
import { FeedbacksubmitComponent } from './feedbacksubmit/feedbacksubmit.component';
import { VerifyEmailComponent } from './verify-email/verify-email.component';

import { SuccessPaymentComponent } from "./register/success-payment/success-payment.component";
import { PaymentFailedComponent } from './register/payment-failed/payment-failed.component';
import { PaymentComponent } from './register/payment/payment.component';
import { RenewalComponent } from './register/renewal/renewal.component';

const routes: Routes = [
  {
    path: '',
    children: [
      {
        path: 'comingsoon',
        component: ComingSoonPageComponent,
        data: {
          title: 'Coming Soon page'
        }
      },
        {
          path: 'renewal',
          component: RenewalComponent,
          data: {
            title: 'Membership Renewal'
          }
        },
      {
        path: 'error',
        component: ErrorPageComponent,
        data: {
          title: 'Error Page'
        }
      },
      {
        path: 'forgotpassword',
        component: ForgotPasswordPageComponent,
        data: {
          title: 'Forgot Password Page'
        }
      },

      {
        path: 'lockscreen',
        component: LockScreenPageComponent,
        data: {
          title: 'Lock Screen page'
        }
      },
      {
        path: 'login',
        component: LoginPageComponent,
        data: {
          title: 'Login Page'
        }
      },
      {
        path: 'maintenance',
        component: MaintenancePageComponent,
        data: {
          title: 'Maintenance Page'
        }
      },
      {
        path: 'orgregister',
        component: OrgRegisterComponent,
        data: {
          title: 'Register Page'
        }
      },
      {
        path: 'register',
        component: RegisterPageComponent,
        data: {
          title: 'Register Page'
        },
        children:[
        ]
      },
      {
        path: 'feedbacksubmit',
        component: FeedbacksubmitComponent,
        data: {
          title: 'Submit Page'
        }
      } ,
      {
        path: 'verify-email',
        component: VerifyEmailComponent,
        data: {
          title: 'Email Verification'
        }
      },
      {
        path: 'payment',
        component: PaymentComponent,
        data: {
          title: 'Payment'
        }
      },
      {
        path: 'paymentFail',
        component: PaymentFailedComponent,
        data: {
          title: 'Payment Fail'
        }
      },
      {
        path: 'paymentSuccess',
        component: SuccessPaymentComponent,
        data: {
          title: 'Payment Success'
        }
      }

    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ContentPagesRoutingModule { }
