import { Component, ViewChild, ElementRef ,OnInit } from '@angular/core';
import {formatDate } from '@angular/common';
import { Router, ActivatedRoute } from "@angular/router";
import { UserService } from '../../../../shared/data/user.service';

declare function calcSHA1(a): any;
declare function charToByte(a): any;
declare function byteToHex(a): any;

@Component({
  selector: 'app-renewal',
  templateUrl: './renewal.component.html',
  styleUrls: ['./renewal.component.scss']
})
export class RenewalComponent implements OnInit {
  @ViewChild("storename") storename: ElementRef;
  @ViewChild("sharedsecret") sharedsecret: ElementRef;
  @ViewChild("chargetotal") chargetotal: ElementRef;
  @ViewChild("currency") currency: ElementRef;
  @ViewChild("hash") hash: ElementRef;
  @ViewChild("txndatetime") txndatetime: ElementRef;
  @ViewChild("payForm") payForm;
  user: any;
  amount = 0;
  userId = 0;
  constructor(public userService: UserService, private router: Router, private route: ActivatedRoute) {
  }
  ngOnInit(){
    console.log(`currentUser ${JSON.stringify(localStorage.getItem('currentUser'))}`);
    if(localStorage.getItem('currentUser')){
      this.user = JSON.parse(localStorage.getItem('currentUser'));
      this.amount = this.user.amount;
    }
    else{
      this.route.queryParams.subscribe(params => {this.userId = params['id'];});
      this.userService.getUser(this.userId, new Date().getTime()).then(data => {
        if(data['status'] == 'success'){
          this.user = data['user'][0];
          localStorage.setItem('currentUser', this.user);
          this.amount = this.user.amount;
          console.log(this.user);
        }
      });
    }
  }

  onSubmit() {
    if(localStorage.getItem('currentUser')){
      this.createHash(this.user.amount);
      let tdetails ={
        uid: this.user.id,
        membership_id:this.user.membership_id,
        amount:this.user.amount,
        paymentDetails:this.hash.nativeElement.value,
        transactionDate:this.txndatetime.nativeElement.value.split('-')[0]
      };
      localStorage.setItem('tdetails', JSON.stringify(tdetails));
      this.payForm.nativeElement.submit();
    }
  }

  createHash(tp) {
    let today = new Date();
    let formattedDate =formatDate(today, 'yyyy:MM:dd-H:mm:ss', 'en-US');
    var storeId = this.storename.nativeElement.value;
    var sharedSecret ="h48HabpQmW";
    var chargetotal = tp;
    var currency = this.currency.nativeElement.value;
    var stringToHash = storeId + formattedDate + chargetotal + currency + sharedSecret;
    var ascii = this.getHexFromChars(stringToHash);
    var hash = calcSHA1(ascii);
    this.hash.nativeElement.value = hash;
    this.chargetotal.nativeElement.value = tp;
    this.txndatetime.nativeElement.value = formattedDate;
  }
  getHexFromChars(value) {
   var char_str = value;
   var hex_str = "";
   var i, n;
   for (i = 0; i < char_str.length; i++) {
     n = charToByte(char_str.charAt(i));
     if (n != 0) {
       hex_str += byteToHex(n);
     }
   }
   return hex_str.toLowerCase();
 }

}
